:root {
  --background-color: #121212;
  --surface-color: #1e1e1e;
  --primary-color: #bb86fc;
  --primary-variant-color: #3700b3;
  --secondary-color: #03dac6;
  --on-primary-color: #000;
  --on-surface-color: #fff;
  --error-color: #cf6679;
  --success-color: #4caf50;
  --fail-color: #f44336;
  --neutral-color: #888;
  color-scheme: dark;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
}

body {
  background-color: var(--background-color);
  color: var(--on-surface-color);
  box-sizing: border-box;
  justify-content: center;
  align-items: flex-start;
  min-height: 100vh;
  margin: 0;
  padding: 2rem;
  display: flex;
}

.container {
  flex-direction: column;
  gap: 1.5rem;
  width: 100%;
  max-width: 1200px;
  display: flex;
}

h1 {
  color: var(--on-surface-color);
  text-align: center;
  margin: 0;
  font-size: 2.5rem;
  font-weight: 700;
}

p {
  text-align: center;
  color: var(--on-surface-color);
  opacity: .7;
  margin: -1rem 0 0;
}

.input-group {
  flex-direction: column;
  gap: .75rem;
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  display: flex;
}

textarea {
  background-color: var(--surface-color);
  color: var(--on-surface-color);
  resize: vertical;
  border: 1px solid #fff3;
  border-radius: 8px;
  outline: none;
  flex-grow: 1;
  min-height: 80px;
  padding: .75rem 1rem;
  font-family: inherit;
  font-size: 1rem;
  transition: border-color .2s;
}

textarea:focus {
  border-color: var(--primary-color);
}

button {
  background-color: var(--primary-color);
  color: var(--on-primary-color);
  cursor: pointer;
  border: none;
  border-radius: 8px;
  padding: .75rem 1.5rem;
  font-size: 1rem;
  font-weight: 600;
  transition: background-color .2s, opacity .2s;
}

button:hover {
  background-color: var(--primary-variant-color);
  color: var(--on-surface-color);
}

.input-group > button {
  align-self: flex-end;
}

button:disabled {
  opacity: .5;
  cursor: not-allowed;
}

.loader {
  justify-content: center;
  align-items: center;
  padding: 2rem;
  display: flex;
}

.loader:after {
  content: "";
  border: 4px solid var(--surface-color);
  border-top-color: var(--primary-color);
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: 1s linear infinite spin;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.hidden {
  display: none !important;
}

.error {
  color: var(--error-color);
  text-align: center;
  background-color: #cf667933;
  border-radius: 8px;
  max-width: 800px;
  margin: 0 auto;
  padding: 1rem;
}

.filter-bar {
  flex-wrap: wrap;
  justify-content: center;
  gap: .75rem;
  margin-bottom: -.5rem;
  display: flex;
}

.filter-button {
  background-color: var(--surface-color);
  color: var(--on-surface-color);
  border: 1px solid #0000;
  padding: .5rem 1rem;
  font-size: .9rem;
  font-weight: 500;
}

.filter-button:hover {
  background-color: var(--surface-color);
  color: var(--primary-color);
  border-color: var(--primary-color);
}

.filter-button.active {
  background-color: var(--primary-variant-color);
  color: var(--on-surface-color);
  border-color: var(--primary-color);
}

.results-header {
  justify-content: flex-end;
  display: flex;
}

.download-button {
  color: var(--secondary-color);
  border: 1px solid var(--secondary-color);
  background-color: #0000;
  font-weight: 500;
}

.download-button:hover {
  color: var(--secondary-color);
  background-color: #03dac61a;
}

.results-container {
  border-radius: 12px;
  flex-direction: column;
  gap: 2rem;
  display: flex;
}

.sentiment-breakdown {
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 1.5rem;
  display: grid;
}

.sentiment-card {
  background-color: var(--surface-color);
  border-top: 4px solid;
  border-radius: 12px;
  flex-direction: column;
  gap: 1rem;
  padding: 1.5rem;
  display: flex;
}

.sentiment-card.positive {
  border-color: var(--success-color);
}

.sentiment-card.neutral {
  border-color: var(--neutral-color);
}

.sentiment-card.negative {
  border-color: var(--fail-color);
}

.sentiment-card h2 {
  text-align: center;
  color: var(--on-surface-color);
  margin: 0;
  font-size: 1.5rem;
}

.sentiment-card.positive h2 {
  color: var(--success-color);
}

.sentiment-card.neutral h2 {
  color: var(--neutral-color);
}

.sentiment-card.negative h2 {
  color: var(--fail-color);
}

.percentage {
  text-align: center;
  margin: -.5rem 0;
  font-size: 3rem;
  font-weight: 700;
}

.comment-count {
  text-align: center;
  opacity: .7;
  margin: -1rem 0 0;
  font-size: .9rem;
}

.sentiment-card h3 {
  text-transform: uppercase;
  letter-spacing: .5px;
  opacity: .7;
  border-bottom: 1px solid #ffffff1a;
  margin: 0;
  padding-bottom: .5rem;
  font-size: 1rem;
  font-weight: 600;
}

.word-cloud-container {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 200px;
  display: flex;
}

.word-cloud-container svg {
  display: block;
}

.comments-list {
  flex-direction: column;
  gap: .75rem;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.comment {
  background-color: var(--background-color);
  opacity: .9;
  border-left: 2px solid var(--primary-color);
  border-radius: 6px;
  padding: .75rem 1rem;
  font-size: .95rem;
  font-style: italic;
}

.comment:before {
  content: "“";
  margin-right: .25rem;
  font-weight: bold;
}

.comment:after {
  content: "”";
  margin-left: .25rem;
  font-weight: bold;
}
